.wrapper{
  padding-top: 12vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  border:1px solid red;
}
.heading{
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 3rem;
}

@media only screen and (min-width: 800px){
  .wrapper{
    padding-top: 20vh;
  } 
  .heading{
    font-size: 2.5rem;
  }
}
